.App {
  text-align: center;
}

#wrapper {
  max-width: calc(100vw - 2rem);
  margin: 0 auto;
  padding: 1rem;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}

#trigger {
  align-self: center;
  margin: 0.5em;
  width: 300px;
  height: 50px;
  border: none;
  background-color: limegreen;
  color: white;
  font-size: 1rem;
  cursor: pointer;
  border-radius: 3px;
  transition: background-color 0.3s, color 0.3s;
}

#recognition-result {
  align-self: center;
  width: 320px;
  height: 200px;
  margin-top: 0.5em;
  font-size: 1.5em;
}

#partial {
  align-self: center;
  width: 320px;
  height: 30px;
  font-size: inherit;
  margin-top: 0.5em;
}

#bottom-button-row {
  align-self: center;
  margin-top: 0.5em;
}

#share {
  align-self: center;
  margin: 0.5em;
  width: 100px;
  height: 50px;
  border: none;
  background-color: blue;
  color: white;
  font-size: 1rem;
  cursor: pointer;
  border-radius: 3px;
  transition: background-color 0.3s, color 0.3s;
}

#clear {
  align-self: center;
  margin: 0.5em;
  width: 100px;
  height: 50px;
  border: none;
  background-color: red;
  color: white;
  font-size: 1rem;
  cursor: pointer;
  border-radius: 3px;
  transition: background-color 0.3s, color 0.3s;
}

@media (min-width: 320px) {
  #wrapper {
    flex-direction: column;
  }

  #trigger {
    margin: 0.5em auto;
    height: 70px;
  }
}
